@import '../../styles/variables.scss';

.media {
    background: #111;

    .container {
        padding: 2rem;
        // padding-top: 0;
    }


}

.video-container {
    display: flex;
    justify-content: center;
    // align-items: center;
    flex-wrap: wrap;
    row-gap: 2rem;
    column-gap: 2rem;
    max-width: 100%;
    height: 100%;
    margin-bottom: 2rem;

    @each $preview in 1,
    2,
    3,
    4 {
        .preview-#{$preview} {
            background: url(../../images/preview#{$preview}.jpg) no-repeat center;

        }
    }

    .video-container__item {
        // flex-basis: 31.8rem;
        flex-basis: 31%;
        // width: 700px;
        // height: 300px;
        position: relative;
        // padding-top: 56.25%;
        background-size: 100%;
        opacity: 0.4;
        border-radius: 0.3rem;
        box-shadow: 0.4rem 0.4rem 0.3rem black, 0.5rem 0rem 5rem rgba(255, 255, 255, 0.218);
        transition: opacity 0.4s ease, box-shadow 0.2s ease;
        aspect-ratio: 16/9;
        min-width: 0;
        // transform-style: preserve-3d;

        .video__play-icon {

            width: 6rem;
            height: 6rem;
            background: url(../../images/icons/icons8-youtube-play.svg) no-repeat 100%/cover;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%) translateY(-50%) translateZ(60px);
            // transform: translateZ(60px);
            transition: width 0.2s ease, height 0.2s ease;
        }

        &:hover {
            cursor: pointer;
            opacity: 1;
            box-shadow: 0.8rem 1rem 0.3rem black, 0.5rem 0rem 5rem rgba(255, 255, 255, 0.39);

            .video__play-icon {
                width: 8rem;
                height: 8rem;
            }
        }

        .video-iframe {
            width: 100%;
            height: 100%;
        }
    }
}

.preview-4 {
    order: -1;
}